body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Home {
  background-color:black;
  height:100%;
  width: 100%;
  position: fixed;
  z-index: 0;
  opacity: 0.8;
  overflow-x: hidden;
  text-align:left;
  color: white;
}

.contact-icon {
  color: white;
  margin: 5px;
}
.contact-icon :hover{
  color: red;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.introduction{
  font-size: 30px;
  padding-left: 60px;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  color: white;
}

.introduction > *{
  margin: 0px;
}

.NavbarButton {
    font-size: 50px;
    cursor: pointer;
    position: fixed;
    z-index: 2;
    top:0;
    right:0;
}

hr {
    width: 80%;
}

.repo-url{
    font-size: 27px;
    padding-left: 5px;
    text-decoration: none;
    color: white;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

.repo{
    background-color: black;
    border: 1px solid white ;
    border-width: thin;
    border-radius: 25px;
    list-style-position:inside;
    display: block;
    margin: 0 0 10px 0;
}

.repo a:hover{
    color: red;
    text-decoration: underline;
}

.repo-desc{
    padding-left: 5px;
    font-size: 14px;
    text-decoration: none;
    color: white;   
}
.portfolio {
    background-color: black;
    height: 100%;
    width: 100%;
    position: fixed;
    opacity: 0.8;
    color: white;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.portfolio-frame{
    right: 0;
    background-color: black;
    height: 80%;
    overflow: scroll;
    overflow-x: hidden;
}

.portfolio-list{
    text-align: center;
}
.Navbar{
  height:100%;
  width: 50%;
  position: fixed;
  z-index: 1;
  top: 0;
  right: 0;
  opacity: 1;
  background-color: black;
  overflow-x: hidden;
  -webkit-transition: 0.7s;
  transition: 0.7s;
  display: -webkit-flex;
  display: flex;  
  -webkit-flex-flow: column wrap;  
          flex-flow: column wrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.Navbar a {
  padding: 1rem 0px 1rem 0px;
  text-decoration: none;
  font-size: 25px;
  color: #f1f1f1;

  -webkit-justify-content: center;

          justify-content: center;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.Navbar a:hover{
  color: #818181;
}

@media screen and (max-height: 450px) {
  .Navbar {padding-top: 15px;}
  .Navbar a {font-size: 18px;}
}

.Navbarbranding {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-flow: column wrap;
          flex-flow: column wrap;
  -webkit-justify-content: center;
          justify-content: center;
  height:100%;
  width: 50%;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  opacity: 0.5;
  background-color: white	; 
  -webkit-transition: 0.4s; 
  transition: 0.4s;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

